@mixin bgi {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

@mixin onTablet {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1200px) {
    @content;
  }
}
