.page {
  background-color: $colorPink;
  scroll-behavior: smooth;

  &__body--with-menu {
    overflow: hidden;
  }

  &__menu {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }
}
